<template  >
  <div class="content">
      <form action="" @submit.prevent="submit">
        <div class="form-warp">
          <input class="Input" type="text" placeholder="会议名称" v-model="meeting" ref="meetings"/>
          <i class="iconfont">&#xe75f;</i>
        </div>
        <div class="form-warp">
          <el-date-picker v-model="date" ref="dataTime" class="Input" type="datetime" placeholder="会议日期" ></el-date-picker>
          <i class="iconfont">&#xe615;</i>
        </div>
        <div class="form-warp">
          <el-date-picker v-model="dataStart" ref="start" class="Input" type="datetime" placeholder="请选择开始时间" ></el-date-picker>
          <i class="iconfont">&#xe620;</i>
        </div>
        <div class="form-warp">
          <el-date-picker v-model="dataEnd" ref="end" class="Input" type="datetime" placeholder="请选择结束时间" ></el-date-picker>
          <i class="iconfont">&#xe67f;</i>
        </div>
        <div class="form-warp">
          <el-select v-model="dataLanguage" ref="select" clearable placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              class="Input"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
          <i class="iconfont">&#xe648;</i>
        </div>
        <div class="form-warp">
          <input class="Input" type="password" ref="password" v-model="input.Password" placeholder="请输入会议密码" />
          <i class="iconfont">&#xe678;</i>
        </div>
        <div class="btnicon">
          <section class="model-13">
            <div class="checkbox">
              <input type="checkbox" v-model="input.rtm" checked/>
              <label></label>
            </div>
          </section>
          <section class="model-13">
            <div class="checkbox">
              <input type="checkbox" v-model="input.crm"/>
              <label></label>
            </div>
          </section>
        </div>
        <button type="submit">提交</button>
    </form>
  </div>
</template>

<script>
import 'style/btnIcon.css'
import axios from 'axios'
import Cookies from 'js-cookie'
export default{
  name: 'FormSubmit',
  data () {
    return {
      meeting: '',
      date: '',
      dataStart: '',
      dataEnd: '',
      num: true,
      dataLanguage: '',
      dataPassword: '',
      input: {
        rtm: true,
        crm: false,
        Password: ''
      },
      options: [{
        value: '001',
        label: '中文'
      }, {
        value: '002',
        label: 'English'
      }]
    }
  },
  methods: {
    submit () {
      if (this.$refs.meetings.value === '') {
        this.$refs.meetings.focus()
        return false
      } else if (this.$refs.dataTime.value === '') {
        this.$refs.dataTime.focus()
        return false
      } else if (this.$refs.start.value === '') {
        this.$refs.start.focus()
        return false
      } else if (this.$refs.end.value === '') {
        this.$refs.end.focus()
        return false
      } else if (this.$refs.select.value === '') {
        this.$refs.select.focus()
        return false
      } else if (this.$refs.password.value.length <= 0) {
        this.$refs.password.focus()
        return false
      } else {
        const token = Cookies.get('name')
        axios({
          method: 'get',
          url: '/api/5b1a190c62cb9',
          headers: {'version': 'v3.0', 'access-token': token},
          params: {
            meetingName: this.meeting,
            meetingDate: this.date,
            meetingStart: this.dataStart,
            meetingStop: this.dataEnd,
            meetingPassword: this.input.Password
          },
          responseType: 'json'
        }).then(() => {
          this.$layer.msg('提交成功')
          setInterval(() => { this.$router.push('/ReseEnd') }, 500)
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import "~style/input.styl"
  @import "~style/var.styl"
  .form-warp
    position relative
    width 92%
    margin .6rem 4%
    height $Height
    line-height $Height
  .form-warp  .Input
      width 100%
      height 100%
      line-height $Height
      background #eee
      font-size .16rem
      text-indent .68rem
      border-radius .8rem
  .form-warp .Input::-webkit-input-placeholder
    color #C0C4CC
  .form-warp  i
      position absolute
      display inline-block
      left .2rem
      top .04rem
    button
      Btn()
      position absolute
      bottom 0
      width 100%
      height $Height
      line-height $Height
      font-size .3rem
      border-radius 0
    .btnicon
      display flex
      justify-content center
      width 92%
      margin auto
.el-date-editor >>> .el-input__inner
    width 90%
    height .78rem
    line-height .78rem
    border none
    background #eee
    color #333
    border-radius .8rem
    padding 0
.el-date-editor >>> .el-input__icon
  display none
.form-warp >>> .el-select
  width 100%
.el-select >>> .el-input__inner
  border-color transparent
  text-indent 1rem
  padding 0
  font-size .16rem
  border-radius .8rem
  background #eee
</style>



