  </div>
</template>
<script>
import Message from './Message'
export default{
  name: 'ReseEnd',
  components: {
    Message
  }
}
</script>
<style lang="stylus" scoped>
  .warp
    width 92%
    margin .5rem auto
    border-radius .1rem
    text-align center
    padding-top .4rem
    box-shadow 0 0 2px #e5e5e5
    .Icimg
      width 1.4rem
      height 1.4rem
      margin auto
      img
        width 100%
        height 100%
    p
      margin .4rem 0
      color #333
      font-size .38rem
    span
      display block
      color #666
      padding-bottom .5rem
    i
      color #1C9CF5
</style>



