import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/home/Home'
import Reservation from '@/pages/TimeDate/reservation'
import Message from '@/pages/TimeDate/Message'
import FormSubmit from '@/pages/TimeDate/FormSubmit'
import ReseEnd from '@/pages/TimeDate/ReseEnd'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    }, {
      path: '/Reservation',
      name: 'Reservation',
      component: Reservation
    }, {
      path: '/Message',
      name: 'Message',
      component: Message
    }, {
      path: '/FormSubmit',
      name: 'FormSubmit',
      component: FormSubmit
    }, {
      path: '/ReseEnd',
      name: 'ReseEnd',
      component: ReseEnd
    }
  ]
})



