import Vue from 'vue'
import App from './App'
import router from './router'
import ElementUI from 'element-ui'
import md5 from 'js-md5'
import axios from 'axios'
import Cookies from 'js-cookie'
import { appId, appSecret } from './data'
import 'element-ui/lib/theme-chalk/index.css'
import fastClick from 'fastClick'
import layer from 'vue-layer'
import 'style/reset.css'
import 'style/border.css'
import 'style/iconfont.css'

// Vue.prototype.GLOBAL = global_
Vue.config.productionTip = false
fastClick.attach(document.body)
Vue.prototype.$layer = layer(Vue)
Vue.use(ElementUI)
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.prototype.funcName = function () {
  getHandle()
  function getHandle () {
    var deviceId,
      randStr,
      Tont
    axios({
      method: 'get',
      url: '/api/5b4423d585225',
      headers: {'version': 'v3.0', 'Access-Control-Allow-Origin': '*'},
      changeOrigin: true,
      credentials: 'same-origin',
      responseType: 'json'
    }).then((res) => {
      deviceId = res.data.data.device_id
      randStr = res.data.data.rand_str
    }).then(() => {
      const timestamp = Date.parse(new Date()) / 1000
      let signa = 'app_id=' + appId + '&app_secret=' + appSecret + '&device_id=' + deviceId + '&rand_str=' + randStr + '&timestamp=' + timestamp
      const signature = md5(signa)
      axios({
        method: 'post',
        url: '/api/5b19f193cc234',
        headers: {'version': 'v3.0'},
        data: {
          signature,
          timestamp,
          rand_str: randStr,
          device_id: deviceId,
          app_id: appId
        },
        responseType: 'json'
      }).then((refs) => {
        Tont = refs.data.data.access_token
        Cookies.set('name', Tont)
      })
    })
  }
}



