<template>
  <div class="home">
    <form action="" @submit.prevent="submit">
      <div class="header">
        <div><img src="@/assets/img/logo.png" alt=""></div>
        <div><h2>科天云</h2></div>
      </div>
      <div class="content">
          <div class="wrap">
            <i class="iconfont">&#xe60b;</i>
            <input class="wrap-input-com" type="text"
            @blur.prevent="com" ref="comRef"
            v-model="inputText.com" placeholder="请输入站点名称"/>
            <label for="">.webex.com.cn</label>
            <span class="tip" v-show="!comTips">请输入站点名称</span>
          </div>
          <div class="wrap">
            <i class="iconfont">&#xe636;</i>
            <input class="wrap-input" type="text"
            @blur.prevent="username" ref="userRef"
            v-model="inputText.username" placeholder="请输入账号"/>
            <span class="tip" v-show="!userTips">请输入账号</span>
          </div>
          <div class="wrap">
            <i class="iconfont">&#xe678;</i>
            <input class="wrap-input" type="password"
            @blur.prevent="password" ref="passwordRef"
            v-model="inputText.password" placeholder="请输入密码"/>
            <span class="tip" v-show="!passTips">请输入密码</span>
          </div>
          <div class="forget">
            <!-- <input id="datetime-picker" type="checkbox">
            <label for="">忘记密码</label> -->
          </div>
      </div>
      <div class="footer">
        <button type="submit">登陆 </button>
        <div>
          <img src="" alt="">
        </div>
      </div>
    </form>
  </div>
</template>
<script>
import axios from 'axios'
import Cookies from 'js-cookie'
export default{
  name: 'Home',
  data () {
    return {
      userTips: true,
      passTips: true,
      comTips: true,
      inputText: { }
    }
  },
  methods: {
    com () {
      const com = this.$refs.comRef
      let val = com.value
      const mobile = /[a-zA-Z]/.test(val)
      if (!mobile) {
        com.style.border = '1px solid red'
        this.comTips = false
        com.focus()
      } else {
        com.style.border = '1px solid #fff'
        com.style.borderBottom = '1px solid #1C9CF5'
        this.comTips = true
      }
    },
    username () {
      const name = this.$refs.userRef
      let val = name.value
      const mobile = /^[\u0391-\uFFE5]+$/.test(val)
      if (mobile) {
        name.style.border = '1px solid red'
        this.userTips = false
        name.focus()
      } else {
        name.style.border = '1px solid #fff'
        name.style.borderBottom = '1px solid #1C9CF5'
        this.userTips = true
      }
    },
    password () {
      const password = this.$refs.passwordRef
      let val = password.value
      const mobile = /[^\u4e00-\u9fa5]/.test(val)
      if (!mobile) {
        password.style.border = '1px solid red'
        this.passTips = false
        password.focus()
      } else {
        password.style.border = '1px solid #fff'
        password.style.borderBottom = '1px solid #1C9CF5'
        this.passTips = true
      }
    },
    submit () {
      this.funcName()
      const token = Cookies.get('name')
      let com = this.$refs.comRef
      let name = this.$refs.userRef
      let password = this.$refs.passwordRef
      if (com.value.length <= 0) {
        com.focus()
        return false
      } else if (name.value.length <= 0) {
        name.focus()
        return false
      } else if (password.value.length <= 0) {
        password.focus()
        return false
      } else {
        axios({
          method: 'post',
          url:'/api/5b432334b00c9',
          headers: {'version': 'v3.0', 'Access-Control-Allow-Origin': '*', 'access-token': token},
          data: {
            webExId: this.inputText.username,
            password: this.inputText.password,
            siteName: this.inputText.com
          },
          responseType: 'json'
        }).then((res) => {
          if (res.data.code === 1) {
            this.$layer.msg('登陆成功')
            setInterval(() => { this.$router.push('/Reservation') }, 500)
            return false
          } else {
            this.$layer.msg(res.data.msg)
          }
        })
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
  .wrap >>> .tip
    display block
    width 100%
    font-size 12px
    text-align left
    margin-top .1rem
    color red
  .home
    text-align center
    .header
      margin-top .6rem
      h2
        font-size .64rem
        margin .8rem 0
    .content
      margin-top .6rem
      width 100%
      .wrap
        position relative
        display flex
        width 80%
        margin .3rem auto
        .wrap-input-com
          flex 1
          height .6rem
          color #666
          text-indent .4rem
          border-bottom 1px solid #1C9CF5
        label
          display inline-block
          line-height .6rem
        .wrap-input
          width 100%
          height .6rem
          color #666
          text-indent .4rem
          border-bottom 1px solid #1C9CF5
        i
          display inline-block
          position absolute
          left 0
          top .1rem
        span
          display block
          position absolute
          top .6rem
      .forget
        width 60%
        margin auto
        text-align right
    .footer
      margin-top 1.4rem
      width 100%
      button
        width 80%
        margin auto
        height .6rem
        font-size .32rem
        color #fff
        border 1px solid #fff
        background #1C9CF5
        cursor pointer
</style>



