<template>
  <div class="tips">
    <div v-for="(item, index) of List" :key="index">
      <div class="started" ref="started" @click="list(item.meetingkey)"  v-if="item.meetstatus!== '已结束' && '已过期'">
        <div class="header">
          <span>会议{{item.meetstatus}}</span>
        </div>
        <div class="content">
          会议名称:&nbsp;{{item.meetingname}}
        </div>
        <div class="content">
          开始时间&nbsp;{{item.starttime}}
        </div>
        <div class="content">
          结束时间&nbsp;{{item.stoptime}}
        </div>
        <div class="content">
          <button>取消会议</button>
        </div>
      </div>
      <div class="end" ref="End" v-if="item.meetstatus == '已结束' && '已过期'">
        <div class="header">
          <span>会议{{item.meetstatus}}</span>
        </div>
        <div class="content">
          会议名称:&nbsp;{{item.meetingname}}
        </div>
        <div class="content">
          开始时间&nbsp;{{item.starttime}}
        </div>
        <div class="content">
          结束时间&nbsp;{{item.stoptime}}
        </div>
        <div class="content">
          <button @click="active(index)">删除</button>
        </div>
      </div>
    </div>
    <div class="btn">
      <button @click="jump">预约</button>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
import Cookies from 'js-cookie'
export default{
  name: 'Reservation',
  data () {
    return {
      List: [],
      changeActive: 0
    }
  },
  methods: {
    list (i) {
      setInterval(() => { this.$router.push({path: '/Message', query: {id: i}}) }, 500)
      return false
    },
    active (index) {
      this.changeActive = index
      if (this.$refs.started === undefined) {
        this.$refs.End[index].style.display = 'none'
      } else {
        const star = this.$refs.started.length
        this.$refs.End[index - star].style.display = 'none'
      }
    },
    jump () {
      this.$router.push('/FormSubmit')
    },
    created () {
      const token = Cookies.get('name')
      axios({
        method: 'get',
        url: '/api/5b47103ab2ae5',
        headers: {'version': 'v3.0', 'access-token': token},
        responseType: 'json'
      }).then((res) => {
        const data = res.data
        if (data.code === 1) {
          this.List = data.data
          return false
        }
      })
    }
  },
  mounted () {
    this.created()
  }
}
</script>
<style lang="stylus" scoped>
  .tips >>> .active
    display none
  @import "~style/input.styl"
  .started
    text-align center
    HeaderT()
    .header
      ColorHeander()
    .content
      Cont()
      button
        font-size .14rem
        padding .06rem
        background #fff
        border 1px solid #1C9CF5
        border-radius .08rem
        color #1C9CF5
  .end
    text-align center
    HeaderT()
    .header
      ColorHeander()
      color #999
    .content
      Cont()
      color #999
      button
        font-size .14rem
        width .92rem
        background #fff
        border 1px solid red
        border-radius .08rem
        color red
  .btn
    text-align center
    button
      Btn()
</style>



